<template>
<div class="card">
  <chart :options="option" ref="c1"></chart>
</div>
</template>

<style>
.card,
.card > .echarts {
  width: 100%;
  height: 100%;
}
</style>

<script>
import _ from "underscore";
import ds from "../datasource";

let chart = null;
const color = ["#a6c84c", "#F06C00"];

const getScatter = params => {
  return {
    name: params.stationId ? "dest-scatter" : "station-scatter",
    type: "effectScatter",
    coordinateSystem: "bmap",
    data: (() => {
      const stations = params.stationId
        ? [params.station, ...params.station.dest]
        : params.stations;

      return stations.map(({ id, name, lng, lat, count, path }) => ({
        id,
        name,
        value: [lng, lat, params.stationId ? path : count]
      }));
    })(),
    symbolSize: val => {
      return params.stationId ? 5 + val[2] : 5 + val[2] / 100;
    },
    symbol: "pin",
    symbolRotate: 0,
    label: {
      normal: {
        formatter: params => `${params.name}: ${params.value[2]}`,
        position: "right",
        show: false
      },
      emphasis: {
        show: false
      }
    },
    itemStyle: {
      normal: {
        color: color[1]
      }
    }
  };
};

const getLineData = params => {
  const fromStation = params.station;
  return params.station.dest.map(toStation => ({
    fromName: fromStation.name,
    toName: toStation.name,
    count: toStation.path,
    coords: [[fromStation.lng, fromStation.lat], [toStation.lng, toStation.lat]]
  }));
};

const getSeries = stationId => {
  const params = {
    stationId,
    stations: stationId ? null : ds.getStations(),
    station: stationId ? ds.getStationExt(stationId) : null
  };
  return stationId
    ? [
        {
          name: "dest-lines-1",
          type: "lines",
          coordinateSystem: "bmap",
          zlevel: 1,
          effect: {
            show: true,
            period: 6,
            trailLength: 0.7,
            color: "#fff",
            symbolSize: 5
          },
          lineStyle: {
            normal: {
              color: color[1],
              width: 0,
              curveness: 0.2
            }
          },
          data: getLineData(params)
        },
        {
          name: "dest-lines-2",
          type: "lines",
          coordinateSystem: "bmap",
          zlevel: 2,
          symbol: ["none", "arrow"],
          symbolSize: 10,
          effect: {
            show: true,
            period: 6,
            trailLength: 0,
            // symbol: planePath,
            symbolSize: 5
          },
          lineStyle: {
            normal: {
              color: color[1],
              width: 2,
              opacity: 0.6,
              curveness: 0.2
            }
          },
          data: getLineData(params)
        },
        getScatter(params)
      ]
    : [getScatter(params)];
};

const selectIndex = stationId => {
  chart.setOption(
    _.defaults(
      {
        series: getSeries(stationId)
      },
      chart.getOption()
    ),
    true
  );
};

const option = {
  bmap: {
    center: [120.137184, 30.269041],
    zoom: 12,
    roam: true
  },
  backgroundColor: "#404a59",
  title: {
    text: "e-cars' stations and paths",
    // subtext: "数据纯属虚构",
    left: "center",
    textStyle: {
      color: "#666"
    }
  },
  tooltip: {
    trigger: "item",
    formatter: params => {
      return _.result(
        {
          lines: `${params.data.fromName} -> ${params.data.toName}: ${
            params.data.count
          }`,
          effectScatter: `${params.name}: ${params.value && params.value[2]}`
        },
        params.seriesType,
        ""
      );
    }
  },
  legend: {
    orient: "vertical",
    top: "bottom",
    left: "right",
    data: ds.getStations().map(({ name }) => name),
    textStyle: {
      color: "#000"
    },
    selectedMode: "single"
  },
  series: getSeries()
};

export default {
  data: function() {
    return {
      option
    };
  },
  mounted() {
    chart = this.$refs.c1.chart;
    chart.on("click", e => {
      selectIndex(e.data.id);
    });
    chart.getZr().on("click", e => {
      // click on chart element, already handled by chart.on("click")
      if (e.target) {
        return;
      }
      selectIndex(null);
    });
  },
  methods: {}
};
</script>



