import _ from 'underscore'
import stations from './stations'

const getStations = (() => {
  const result = _.chain(stations)
    .map((station) => _.omit(station, 'dest', 'source'))
    .value()

  return () => _.clone(result)
})()

const getStation = (id) => {
  return _.chain(stations)
    .result(id, {})
    .clone()
    .value()
}

const getStationProp = (id, prop) => {
  return _.chain(getStation(id))
    .result(prop, [])
    .map(thatStation => {
      return _.chain(thatStation)
        .extend({
          path: thatStation.count
        })
        .omit('count')
        .extend(_.chain(getStation(thatStation.id))
          .pick('name', 'lng', 'lat', 'count')
          .value()
        )
        .value()
    })
    .clone()
    .value()
}

const getStationExt = (id) => {
  return _.chain(getStation(id))
    .extend({
      dest: getStationProp(id, 'dest'),
      source: getStationProp(id, 'source')
    })
    .value()
}

export default {
  getStations,
  getStation,
  getStationExt
}



